@import './paginator';

.goods-wrapper {
  max-width: $widthTemplate;
  height: 100%;
  margin: 0 auto;
  padding: 1.6rem 4.2rem;

  @media (max-width: 640px) {
    padding: 0rem;
  }
}

.prod__box {
  display: flex;
}

.prod-photo__box {
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
}

.prod-photo {
  overflow: hidden;
  width: 90%;
  cursor: pointer;

  &__item {
    width: 100%;
    height: auto;
    object-fit: cover;
    border-radius: 1.2rem;
  }
}


.prod-item__info {
  width: 50%;
  min-height: 100%;
}

.prod-brand__title {
  font-family: 'Poppins';
  font-weight: 600;
  font-size: 3.2rem;
  line-height: 140%;
  margin: 0 0 1rem 0;
}

.prod__rating {
  display: flex;
  flex-direction: row;
  margin: 0 0 4.2rem 0;

  @media (max-width: 640px) {
    margin: 0 0 1rem 0;
  }
}

.rating_star {
  width: 1.9rem;
  height: 1.8rem;

  &_fill {
    background: url('./assets/icons/Rating_star_filled.svg') no-repeat;
  }

  &_empty {
    background: url('./assets/icons/Rating_star_empty.svg');
  }
}

.prod__description {
  font-family: 'Open Sans';
  font-weight: 400;
  font-size: 1.7rem;
  line-height: 130%;
  text-transform: capitalize;
  margin: 0 0 4rem 0;

  @media (max-width: 640px) {
    margin: 0 0 1rem 0;
  }
}

.prod__category {
  font-family: 'Open Sans';
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 130%;
  text-decoration: underline;
  white-space: nowrap;

  &_price {
    font-size: 2.6rem;
    font-weight: 600;
    line-height: 150%;
    text-decoration: none;
    color: $primaryColor2;
  }

  &_fullprice {
    text-decoration: line-through;
    color: $textColor1;
  }
}

.prod__changer {
  height: 3rem;
}

.prod__subtitle {
  display: inline-block;
  width: 10rem;
  font-family: 'Open Sans';
  font-weight: 400;
  font-size: 1.4rem;
  line-height: 130%;
  text-decoration: none;
  text-transform: capitalize;
  color: $textColor1;
}

.prod__specs {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
  margin: 0 0 4rem 0;

  @media (max-width: 640px) {
    margin: 0 0 1rem 0;
  }
}

.prod__price {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.product__quantity {
  margin-right: 1.6rem;
}

.prod-photo__popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 100%;

  &_active {
    display: block;
    z-index: 22;
  }
}

.popup__item {
  border-radius: 1.2rem;
  max-width: 100%;
  height: 100%;
  object-fit: cover;
}

.prod-order__box {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  margin: 0 0 3.2rem 0;

  @media (max-width: 640px) {
    margin: 0 0 1rem 0;
    flex-direction: column;
  }
}


